/* =====================================================
   4. TinyMCE styles
   ===================================================== */

.alignleft {
    float: left;
    margin: 0.6em 2em 1em 0;
}

.alignright {
    float: right;
    margin: 0.6em 0 1em 2em;
    text-align: right;
}

.aligncenter {
    clear: both;
    display: block;
    margin: 1em auto 2em;
    text-align: center;
}

.alignnone {
    margin-bottom: 2em;
    margin-top: 1em;
}

.gallery-caption,
.wp-caption-text {
    font-size: 11px;
    font-style: italic;
    margin: 0.5em 0.2em 0;
}