/* =====================================================
   2. Global styles
   ===================================================== */

* {
    &::selection {
        background: darken($accent-color, 8%);
        color: #ffffff;
    }
    &::-moz-selection {
        background: darken($accent-color, 8%);
        color: #ffffff;
    }
}

*,
*:before,
*:after {
    box-sizing: border-box;
}

html,
input,
button,
select,
textarea {
    font: #{$base-font-size}/#{$base-line-height} $base-font-regular;
    color: $base-color;

    -webkit-font-smoothing: antialiased;
    -webkit-text-size-adjust: 100%;
}

body {
    font: inherit;
    color: inherit;
    min-width: 320px;
}

a {
    color: $accent-color;
    outline: none;
}

img {
    display: block;
    margin: 0;
}

input,
textarea {
    &:focus {
        outline: none;
    }
}

input::-ms-clear {
    display: none;
}

.screen-reader-text {
    clip: rect(1px, 1px, 1px, 1px);
    height: 1px;
    overflow: hidden;
    position: absolute !important;
    width: 1px;
}